


















import pageHead from '../../components/page-head.vue';

export default{
	data:{
		allType: [],
		title: "Categories"
	},
	methods:{
		getGoodsType:function(){
			var _this = this;
			wx.request({
				url: 'https://www.slluks.com/index.php/app/goods/goodsType', //仅为示例，并非真实的接口地址
				data: {
					
				},
				header: {
					'content-type': 'application/json' // 默认值
				},
				success: function(res) {
					_this.allType = res.data;
				},
				fail: function(){
					console.log('加载失败');
				}
			})
		}
	},
	onLoad(){
		this.getGoodsType();
	},
	onPullDownRefresh(){
		this.getGoodsType();
	},
	onReachBottom(){
		
	},
	onTabItemTap(){
		this.getGoodsType();
		uni.pageScrollTo({
				scrollTop: 0,
				duration: 300
		});
		uni.showToast({
			title: '刷新成功',
			duration: 2000,
			icon: 'none'
		});
	}
}



